import Link from 'next/link';

const mockQuestion: Question = {
  id: '1',
  title: 'How to properly use React hooks for data fetching?',
  description: `I'm trying to fetch data from an API in my React component using hooks, but I'm running into some issues with infinite loops and stale data. I've seen examples using \`useEffect\` and \`useState\`, but I'm not sure what the best practice is.\n\nHere's a simplified version of my code:\n\n\`\`\`javascript\nfunction MyComponent() {\n  const [data, setData] = useState(null);\n\n  useEffect(() => {\n    fetch('/api/data')\n      .then(res => res.json())\n      .then(setData);\n  }); // No dependency array\n\n  return <div>{data ? JSON.stringify(data) : 'Loading...'}</div>;\n}\n\`\`\`\n\nWhat is the correct way to structure this to avoid re-fetching on every render? Should I be using a library like SWR or React Query?`,
  author: { name: 'Alice', avatarUrl: 'https://placehold.co/40x40.png' },
  tags: ['react', 'hooks', 'data-fetching', 'api'],
  votes: 125,
  answersCount: 2,
  views: 2300,
  createdAt: new Date(Date.now() - 1000 * 60 * 60 * 24 * 3),
  isAccepted: true,
};

const mockAnswers: Answer[] = [
  {
    id: 'ans1',
    content: `You're getting an infinite loop because your \`useEffect\` hook doesn't have a dependency array. This means it runs after every single render. Since you're updating state inside the effect (\`setData\`), it causes a re-render, which triggers the effect again. \n\nTo fix this, you should provide an empty dependency array \`[]\` to the \`useEffect\` hook. This tells React to only run the effect once, after the initial render.\n\nCorrected code:\n\`\`\`javascript\nuseEffect(() => {\n  fetch('/api/data')\n    .then(res => res.json())\n    .then(setData);\n}, []); // <-- Add this empty array\n\`\`\`\n\nUsing a library like **SWR** or **React Query** is highly recommended for more complex scenarios, as they handle caching, revalidation, and error handling for you out of the box.`,
    author: { name: 'Bob', avatarUrl: 'https://placehold.co/40x40.png' },
    votes: 42,
    createdAt: new Date(Date.now() - 1000 * 60 * 60 * 23),
    isAccepted: true,
  },
  {
    id: 'ans2',
    content: `Great answer from Bob. Just to add, you should also handle loading and error states properly. \n\nHere's a more robust example:\n\`\`\`javascript\nfunction MyComponent() {\n  const [data, setData] = useState(null);\n  const [loading, setLoading] = useState(true);\n  const [error, setError] = useState(null);\n\n  useEffect(() => {\n    fetch('/api/data')\n      .then(res => {\n        if (!res.ok) throw new Error('Network response was not ok');\n        return res.json();\n      })\n      .then(data => {\n        setData(data);\n        setLoading(false);\n      })\n      .catch(error => {\n        setError(error);\n        setLoading(false);\n      });\n  }, []);\n\n  if (loading) return <div>Loading...</div>;\n  if (error) return <div>Error: {error.message}</div>;\n\n  return <div>{JSON.stringify(data)}</div>;\n}\n\`\`\``,
    author: { name: 'Charlie', avatarUrl: 'https://placehold.co/40x40.png' },
    votes: 15,
    createdAt: new Date(Date.now() - 1000 * 60 * 60 * 18),
    isAccepted: false,
  }
];

function VoteButtons({ votes, isVertical = false }: { votes: number, isVertical?: boolean }) {
  const direction = isVertical ? 'flex-col' : 'flex-row';
  return (
    <div className={`flex items-center gap-2 ${direction}`}>
      <Button variant="outline" size="icon" className="h-9 w-9">
        <ArrowUp className="h-5 w-5" />
      </Button>
      <span className="font-bold text-lg">{votes}</span>
      <Button variant="outline" size="icon" className="h-9 w-9">
        <ArrowDown className="h-5 w-5" />
      </Button>
    </div>
  );
}

function ContentBody({ content }: { content?: string }) {
    if (!content) return null;
    const htmlContent = content
        .replace(/</g, "&lt;")
        .replace(/>/g, "&gt;")
        .replace(/\n/g, '<br/>')
        .replace(/`([^`]+)`/g, '<code class="bg-muted text-muted-foreground rounded px-1 py-0.5">$&</code>')
        .replace(/```javascript\b/g, '<pre class="bg-muted p-4 rounded-md text-sm overflow-x-auto"><code>')
        .replace(/```/g, '</code></pre>');
    
    return <div className="space-y-4" dangerouslySetInnerHTML={{ __html: htmlContent }} />;
}

